<template>
  <div class="page">
    <el-pagination
      background
      :page-size="size"
      @size-change="handleChangeSize"
      @current-change="handleChangePage"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>
<script>
  export default {
    name: 'page',
    props: {
      total: {
        type: Number,
        default: 0
      },
      size: {
        type: Number,
        default: 10
      },
      page: {
        type: Number,
        default: 1
      }
    },
    data() {
      return {
        currentSize: 10,
        currentPage: 1
      }
    },
    methods: {
      // 向父键上传参数size
      handleChangeSize(val) {
        this.$emit('handleSizeChange', val)
      },
      // 向父键上传参数page
      handleChangePage(val) {
        this.$emit('handlePageChange', val)
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .page
    float: right
</style>



