<template>
  <div class="tree">
    <div class="title">
      <p>{{ title }}</p>
      <el-form>
        <el-button class="el-icon-document button" type='text'>编辑</el-button>
        <el-button class="el-icon-circle-check-outline button" type='text'>保存</el-button>
        <el-button class="el-icon-circle-close-outline button" type='text'>取消</el-button>
      </el-form>
    </div>
    <el-tree :data="treeData">
    </el-tree>
  </div>
</template>
<script>
  export default {
    props: {
      title: {
        type: String,
        default: 'Policy Number 1'
      },
      treeData: {
        type: Array,
        default: () => []
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .tree
    margin: 0 0 0 10px
    .title
      padding 0 10px
      display: flex
      align-items: center
      justify-content: space-between
      background-color: #3e7af6
      .button, p
        color: #ffffff
    .el-tree
      min-height: 400px
      padding: 0 20px
      background-color: #cce0ff
      color: #000080
</style>



