<template>
  <div class="cluster-container">
    <div class="title">
      <p>Cluster</p>
      <p>( <span>{{ clusters.length }}</span> )</p>
    </div>
    <ul>
      <li
        v-for="(cluster, index) in clusters"
        :key="cluster.cluster"
        :class="{'active-dataSets': index===activeDataSets}"
        @click="handleChangeCluster(index)">
        <i><i class="circle"></i></i>
        <i class="rule-name">{{cluster.name}}</i>
        <i class="el-icon-arrow-right arrow"></i>
        <i class="el-icon-caret-right caret" v-if="activeDataSets === index"></i>
      </li>
    </ul>
    <el-button @click="handleAddLi" type="primary" class="add-li">
      <i class="el-icon-circle-plus-outline"></i>
      <i>添加Cluster</i>
    </el-button>
  </div>
</template>
<script>
  export default {
    name: 'cluster',
    props: {
      clusters: {
        type: Array,
        default: () => {
          const clusters = []
          for (let i = 1; i < 11; i += 1) {
            clusters.push({ name: `cluster ${i}` })
          }
          return clusters
        }
      }
    },
    data() {
      return {
        activeDataSets: 0
      }
    },
    methods: {
      handleAddLi() {
      },
      handleChangeCluster(num) {
        this.activeDataSets = num
        this.$emit('handleGetCluster', this.activeDataSets)
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .cluster-container
    .title
      height: 50px
      padding: 10px
      color: #3e7af6
      background-color: #f4f7fe
      font-size: 1.5em
      &>p:first-child
        font-weight: bolder
      span
        text-decoration: underline
    ul
      li
        height: 40px
        padding: 0 10px 0 40px
        display: flex
        justify-content: space-between
        cursor: pointer
        i
          display: inline-block
          line-height: 40px
        .caret
          position: relative
          left: 17px
          color: #3e7af6
      li:nth-child(2n)
        background-color: #f2f2f2
      li.active-dataSets
        background-color: #3e7af6
        color: #ffffff
    .add-li
      width: 200px
      border: none
      border-radius: 0
      color: #1a83ff
      background-color: #cce0ff

  .circle
    width: 10px
    height: 10px
    background-color: green
    border-radius: 100%
</style>



