const dataArray = []
for (let i = 10; i > 0; i -= 1) {
  const container = {}
  const cluster = `Cluster ${i}`
  container.cluster = cluster
  container.info = []
  for (let x = 400; x > 0; x -= 1) {
    const info = {}
    info.date = x
    info.alarmSeverity = Math.floor(Math.random() * 3)
    info.rull = 'xxxx'
    info.description = 'ararararar'
    info.hostName = 'ddddd'
    container.info.push(info)
  }
  dataArray.push(container)
}
console.log('dataArray', dataArray)
export default dataArray



