<template>
  <div>
    <header>
      <navbar>
      </navbar>
    </header>
    <section class="main">
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="dashboard" name="first">
          <Dashboard />
        </el-tab-pane>
        <el-tab-pane label="policies" name="second">
          <Policies />
        </el-tab-pane>
      </el-tabs>
    </section>
  </div>
</template>

<script>
  import navbar from '../../components/navbar'
  import Dashboard from './dashboard/index'
  import Policies from './policies/index'

  export default {
    components: {
      navbar,
      Dashboard,
      Policies
    },
    data() {
      return {
        activeName: 'first'
      }
    }
  }
</script>

<style lang="stylus" scoped>
  section
    margin-left 60px
    margin-right 60px

  .main
    width 88%
    margin: 0 auto
    position: relative
    top: 84px

  .el-tab-pane
    text-align center
</style>



