<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      @sort-change="handleSortChange">
      <el-table-column
        sortable
        align="center"
        prop="date"
        label="时间"
        width="100">
      </el-table-column>
      <el-table-column
        label="告警等级"
        align="center"
        width="80">
        <template slot-scope="props">
          <i :class="alarmSeverityGradeCircle(props.row.alarmSeverity)"></i>
          <i>{{ alarmSeverityGrade(props.row.alarmSeverity) }}</i>
        </template>
      </el-table-column>
      <el-table-column
        sortable
        prop="rull"
        align="center"
        label="规则">
      </el-table-column>
      <el-table-column
        prop="description"
        align="center"
        label="事件描述">
      </el-table-column>
      <el-table-column
        sortable
        prop="hostName"
        label="主机名"
        align="center"
        width="100">
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  // import Page from './page'

  export default {
    name: 'table',
    props: {
      tableData: {
        type: Array,
        default: () => []
      }
    },
    methods: {
      alarmSeverityGrade(num) {
        const judgeMap = {
          0: '一般',
          1: '提示',
          2: '警告'
        }
        return judgeMap[num]
      },
      alarmSeverityGradeCircle(num) {
        const judgeMap = {
          0: 'ordinary',
          1: 'hint',
          2: 'warning'
        }
        return judgeMap[num]
      },
      // 向父键上传参数sort
      handleSortChange(sort) {
        this.$emit('handleSort', sort)
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .ordinary, .hint, .warning
    width: 10px
    height: 10px
    display: inline-block
    border-radius: 100%
  .ordinary
    background-color: #3399ff
  .hint
    background-color: #00cc00
  .warning
    background-color: #ff471a
</style>


