<template>
  <div>
    <el-select v-model="value">
      <el-option
        v-for="(value, key) in searchNames"
        :key="key"
        :label="value"
        :value="key">
      </el-option>
    </el-select>
    <el-input
      placeholder="请输入要过滤的关键字"
      class="search-btn"
      v-model="value">
      <el-button
        slot="append"
        class="el-icon-search"
        @click="handleSearch">
      </el-button>
    </el-input>
  </div>
</template>
<script>
  export default {
    name: 'search',
    data() {
      return {
        searchNames: {
          alarmSeverity: '告警等级',
          rull: '规则',
          description: '时间描述',
          hostName: '主机名'
        },
        type: 'alarmSeverity',
        value: ''
      }
    },
    methods: {
      // 向父键上传参数search
      handleSearch() {
        const payload = {
          type: this.type,
          value: this.value
        }
        this.$emit('handleSearch', payload)
      }
    }
  }
</script>
<style lang="stylus" scoped>
  div
    .el-select
      width: 150px
    .el-input
      width: 400px
      border: 1px solid #3e7af6
      border-right: none
      .el-button
        width: 80px
        height: 38px
        border: none
        border-radius: 0
        background-color: #3e7af6 !important
      .el-icon-search
        color: #fff
</style>


