<template>
  <div class="dashboard">
    <Cluster
      class="cluster"
      @handleGetCluster="handleGetCluster($event)" />
    <div>
      <FilterDate class="filter-date" />
      <Search
        class="search"
        @handleSearch="handleSearch($event)" />
      <Table
        :tableData="showInfo"
        @handleSort="handleSort($event)" />
      <Page
        :size="size"
        :page="page"
        :total="filterClusters.length"
        @handlePageChange="handlePageChange($event)"
        @handleSizeChange="handleSizeChange($event)" />
    </div>
  </div>
</template>
<script>
  import Cluster from '../components/cluster'
  import FilterDate from './components/filterDate'
  import Page from './components/page'
  import Search from './components/search'
  import Table from './components/table'
  import dataArray from './dashboard'

  export default {
    name: 'dashboard',
    components: {
      Cluster,
      FilterDate,
      Page,
      Search,
      Table
    },
    data() {
      return {
        clusterData: [],
        filterDate: -1,
        filterGrade: 0,
        filterKeyword: '',
        page: 1,
        size: 10,
        sort: {
          order: '',
          value: ''
        },
        search: {
          type: '',
          value: ''
        }
      }
    },
    computed: {
      // 筛选后的数据，相关联的的子件：Page
      filterClusters() {
        let filterClusters = this.clusterData
        if (filterClusters.length === 0) {
          return filterClusters
        }
        // 根据search筛选数据
        const search = this.search
        if (search.type !== '') {
          filterClusters = filterClusters.filter(item => item[search.type].indexOf(search.value))
        }
        // 根据sort筛选数据
        const sort = this.sort
        const prop = sort.value
        if (sort.order === 'descending') {
          filterClusters = filterClusters.sort((left, right) => right[prop] - left[prop])
        } else if (this.sort.order === 'ascending') {
          filterClusters = filterClusters.sort((left, right) => left[prop] - right[prop])
        }
        return filterClusters
      },
      // 展示后的数据，相关联的的子件：Table
      showInfo() {
        if (this.filterClusters.length === 0) {
          return []
        }
        const size = this.size
        const start = (this.page - 1) * size
        const end = start + size
        return this.filterClusters.slice(start, end)
      }
    },
    methods: {
      // 获取信息
      handleGetCluster(num) {
        const number = num || 0
        this.clusterData = dataArray[number].info
      },
      // 处理上传的参数search
      handleSearch(search) {
        this.search.type = search.type
        this.search.value = search.value
      },
      // 处理上传的参数search
      handlePageChange(val) {
        this.page = val
      },
      // 处理上传的参数size
      handleSizeChange(size) {
        this.size = size
      },
      // 处理上传的参数sort
      handleSort(sort) {
        this.sort.order = sort.order
        this.sort.value = sort.prop
      }
    },
    created() {
      this.handleGetCluster()
    }
  }
</script>
<style lang="stylus" scoped>
  .dashboard
    width: 70%
    margin: 0 auto
    display: flex
    .cluster
      width: 200px
      margin-right: 20px
    .filter-date
      text-align: left
    .search
      text-align: left
      margin: 20px 0
    .page
      text-align: right
</style>



