<template>
  <div class="alertList">
    <span><i class="iconfont icon-screen">&#xe60d;
    </i>
    <i> 查询日期：</i></span>
    <el-button
      v-for="(value, index) in filterDate"
      :key="index"
      @click="handleFilterDate(index)"
       class="history_time"
       :class="{history_time_active:isActive===index}">
       {{ filterDate[index] }}
    </el-button>
    <el-date-picker
      type="daterange"
      range-separator="-"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      size="mini"
      class="el-date-picker">
    </el-date-picker>
  </div>
</template>
<script>
  export default {
    name: 'filterDate',
    data() {
      return {
        isActive: 0,
        filterDate: [
          '今天', '昨天', '近七天', '近30天'
        ]
      }
    },
    methods: {
      // 向父键上传日期筛选参数
      handleFilterDate(num) {
        this.isActive = num
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .alertList
    font-size: 1.5em
    .icon-screen
      color: #5286F7
    .history_time
      color: #3E7AF6
      background: #F4F7FE
      border-color: snow
    .history_time_active
      color: #FFFFFF
      background: #6F97E8
      border-color: snow
</style>


