<template>
  <div class="policies-content">
    <Cluster class="cluster" />
    <Policies class="policies" />
    <div>
      <Search />
      <Tree
        :treeData="treeData" />
    </div>
  </div>
</template>
<script>
  import Cluster from '../components/cluster'
  import Policies from './components/policies'
  import Search from './components/search'
  import Tree from './components/tree'
  import tree from './tree'

  export default {
    name: 'policies',
    components: {
      Cluster,
      Policies,
      Search,
      Tree
    },
    data() {
      return {
        treeData: null
      }
    },
    created() {
      this.treeData = tree
    }
  }
</script>
<style lang="stylus" scoped>
  .policies-content
    width: 70%
    margin: 0 auto
    display: flex
    .policies
      margin-left: 20px
</style>


